<template>
  <div class="rank">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>我的级别</span>
    </header>
    <main class="rank_c">
      <header class="rank_img">
        <img src="../../assets/person/top.png" alt="">
      </header>
      <div class="rank_d">
        <section>
          <img src="../../assets/person/blue.png" alt="" class="b_img" v-if="type >= 1">
          <img src="../../assets/person/black.png" alt="" class="b_img" v-if="type < 1"><br>
          <span>蓝宝石</span>
        </section>
        <p class="line" :class="type >= 2 ? 'line line_1' : 'line'"></p>
        <section>
          <img src="../../assets/person/red.png" alt="" class="b_img" v-if="type >=2">
          <img src="../../assets/person/black.png" alt="" class="b_img" v-if="type < 2"><br>
          <span>红宝石</span>
        </section>
        <p class="line" :class="type >= 3 ? 'line line_2' : 'line'"></p>
        <section>
          <img src="../../assets/person/yellow.png" alt="" class="b_img" v-if="type >=3">
          <img src="../../assets/person/black.png" alt="" class="b_img" v-if="type < 3"><br>
          <span>钻石</span>
        </section>
      </div>
      <div class="rank_d last">
        <section>
          <img src="../../assets/person/orange.png" alt="" class="b_img" v-if="type >= 4">
          <img src="../../assets/person/black.png" alt="" class="b_img" v-if="type < 4"><br>
          <span>黄钻</span>
        </section>
        <p :class="type >= 5 ? 'line line_3' : 'line'"></p>
        <section>
          <img src="../../assets/person/pink.png" alt="" class="b_img" v-if="type >= 5">
          <img src="../../assets/person/black.png" alt="" class="b_img" v-if="type < 5"><br>
          <span>粉钻</span>
        </section>
        <p :class="type === 6 ? 'line line_4' : 'line line_h'"></p>
        <section>
          <img src="../../assets/person/piple.png" alt="" class="b_img" v-if="type === 6">
          <img src="../../assets/person/black.png" alt="" class="b_img" v-if="type < 6"><br>
          <span>紫钻</span>
        </section>
      </div>
    </main>
    <section class="rank_desc">
    </section>
  </div>
</template>
<script>
  export  default {
    data (){
      return {
        //0 普通 1 蓝宝石 2 红宝石 3钻石
          type: 6,
      }
    },
    methods:{
      back(){
        this.$router.go(-1);
      }
    },
    mounted(){
      this.post('home/User/getUserInfo', {}).then(res => {
        if (res.data.code === 10000) {
            this.type = res.data.info.grade;
        }
      })
    }
  }
</script>
<style scoped>
  @import "../../../static/header.css";
  .rank {
    height: calc(100vh);
    background-color: #F2F2F2;
  }
  .rank_img {
    position: relative;
    width: 100%;
    overflow: hidden;
    padding-bottom: 4.73rem;
    background-color: #FFFFFF;
  }
  .rank_img img {
    position: absolute;
    top: 0;
    width: 100%;
    min-height: 100%;
  }
  .rank_d {
    padding: 0.2rem 0.6rem;
    background-color: #FFFFFF;
    display: flex;
    align-items: center;
  }
  .rank_d section{
    text-align: center;
  }
  .rank_d section span {
    display: inline-block;
    color: #444444;
    font-size: 0.26rem;
    margin: 0.14rem 0 0 0;
    width: 1.2rem;
  }
  .line {
    display: inline-block;
    flex: 1;
    height: 2px;
    background-color: #cccccc    ;
    margin: -0.5rem 0.27rem 0 0.27rem;
  }
  .line_2 {
    background-color: #f9c85d;
  }
  .line_1 {
    background-color: #f67b7c    ;
  }
  .line_3 {
    background-color: #f994c6 ;
  }
  .line_4 {
    background-color: #c8a6e8;
  }

  .b_img {
    width: 0.6rem;
  }
  .rank_desc {
    padding: 0.2rem;
  }
  .rank_desc p {
    font-size: 0.26rem;
    color: #444444;
    line-height: 0.34rem;
  }
  .last {
    padding-bottom: 0.4rem;
  }
</style>



